import { loginByEmail, logout, getInfo } from 'api/login';
import Cookies from 'js-cookie';

const user = {
  state: {
    user: '',
    status: '',
    email: '',
    mid: '',
    code: '',
    uid: undefined,
    auth_type: '',
    token: Cookies.get('Admin-Token'),
    name: '',
    avatar: '',
    introduction: '',
    midroles: [],  // 
    roles: [],
    setting: {
      articlePlatform: []
    }
  },

  mutations: {
    SET_AUTH_TYPE: (state, type) => {
      state.auth_type = type;
    },
    SET_CODE: (state, code) => {
      state.code = code;
    },
    SET_TOKEN: (state, token) => {
      state.token = token;
    },
    SET_UID: (state, uid) => {
      state.uid = uid;
    },
    SET_EMAIL: (state, email) => {
      state.email = email;
    },
    SET_INTRODUCTION: (state, introduction) => {
      state.introduction = introduction;
    },
    SET_SETTING: (state, setting) => {
      state.setting = setting;
    },
    SET_STATUS: (state, status) => {
      state.status = status;
    },
    SET_NAME: (state, name) => {
      state.name = name;
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar;
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles;
    },
    SET_MIDROLES: (state, roles) => {
      state.midroles = roles;
    },
    SET_MID: (state, mid) => {
      state.mid = mid;
    },
    LOGIN_SUCCESS: () => {
      console.log('login success')
    },
    LOGOUT_USER: state => {
      state.user = '';
    }
  },

  actions: {
		// 
    LoginByEmail({ commit }, userInfo) {
      const email = userInfo.email.trim();
      return new Promise((resolve, reject) => {
        loginByEmail(email, userInfo.password).then(response => {
          console.log(response);
          if (response.msg == 0) {
            commit('SET_MID', response.mid);
            commit('SET_MIDROLES', [response.type + '']);
            commit('SET_NAME', response.name);
            sessionStorage.setItem('login-id', email);
            sessionStorage.setItem('midroles', JSON.stringify([response.type + '']));
            sessionStorage.setItem('name', response.name);
            resolve();
          } else {
            reject(response);
          }
					// commit('SET_TOKEN', data.token);
					// commit('SET_EMAIL', email);
        }).catch(error => {
          reject(error);
        });
      });
    },


		// 
    GetInfo({ commit, state }) {
      return new Promise((resolve, reject) => {
				// getInfo(state.token).then(response => {
				//   const data = response.data;
				//   commit('SET_ROLES', data.role);
				//   commit('SET_NAME', data.name);
				//   commit('SET_AVATAR', data.avatar);
				//   commit('SET_UID', data.uid);
				//   commit('SET_INTRODUCTION', data.introduction);
				//   resolve(response);
				// }).catch(error => {
				//   reject(error);
				// });
        if (sessionStorage.getItem('midroles')) {
          console.log(11111);
          commit('SET_MIDROLES', JSON.parse(sessionStorage.getItem('midroles')));
          commit('SET_NAME', sessionStorage.getItem('name'));
        }
        if (state.midroles) {
          commit('SET_ROLES', state.midroles);
          resolve({ role: state.midroles });
        } else {
          reject({ msg: 1, err: 'state' });
        }
      });
    },

		// 
    LoginByThirdparty({ commit, state }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_CODE', code);
        loginByThirdparty(state.status, state.email, state.code, state.auth_type).then(response => {
          commit('SET_TOKEN', response.data.token);
          Cookies.set('Admin-Token', response.data.token);
          resolve();
        }).catch(error => {
          reject(error);
        });
      });
    },


		// 
    LogOut({ commit, state }) {
      return new Promise((resolve, reject) => {
        logout(state.token).then(() => {
          // commit('SET_TOKEN', '');
          // commit('SET_ROLES', []);
          // commit('SET_ROLES', []);
          // commit('SET_ROUTERS',[])
					// Cookies.remove('Admin-Token');
          resolve();
        }).catch(error => {
          reject(error);
        });
      });
    },

		//  
    FedLogOut({ commit }) {
      return new Promise(resolve => {
        sessionStorage.removeItem('login-id');
        sessionStorage.removeItem('midroles');
        sessionStorage.removeItem('name');
				// Cookies.remove('Admin-Token');
        resolve();
      });
    },

		// 
    ChangeRole({ commit }, role) {
      return new Promise(resolve => {
        commit('SET_ROLES', [role]);
        commit('SET_TOKEN', role);
        Cookies.set('Admin-Token', role);
        resolve();
      })
    }
  }
};

export default user;



