import fetch from 'utils/fetch';

export function loginByEmail(account, password) {
	const data = {
		account,
		password
	};
	return fetch({
		url: '/recharge/manager/confirmLogin.do',
		// url: '/loginRecharge',
		method: 'post',
		// method: 'get',
		data
	});
}

export function logout() {
	return fetch({
		url: '/recharge/manager/outLogin.do ',
		method: 'post'
	});
}

export function getInfo(token) {
	return fetch({
		url: '/user/info',
		method: 'get',
		params: {token}
	});
}




