//const BASE_URL = 'http://10.16.29.101:8080';
const BASE_URL = '';
const service = options => new Promise((resolve, reject) => {
  $.ajax({
    type: options.method,
    url: BASE_URL + options.url,
    data: options.data,
    dataType: 'json',
    contentType: "application/x-www-form-urlencoded; charset=utf-8", //
    timeout:8000,
    success(data) {
      store.dispatch('setLoad',false)//
      resolve(data)
    },
    error(msg) {
      if (msg.status == 403) {
        store.dispatch('FedLogOut').then(() => {
          window.location.reload();
        }).catch(er => {
          console.log('');
        });
      }
      reject(msg);
    }
  })
})


export default service;



