//loading
const loading= {
  state: {
    isLoad: true
  },
   mutations:{
       setLoad:(state,boolean)=>{
           state.isLoad=boolean
       }
         },
   actions:{
      setLoad({commit},boolean){
           commit('setLoad',boolean)
       }
   }
  
};
export default loading


